import React from 'react'
import { graphql, navigate } from 'gatsby'
import { useFormikContext } from 'formik';
import { FiArrowLeft } from "react-icons/fi";
import { IconButton } from '@chakra-ui/react'
import { set } from 'lodash';
import AniLink from "gatsby-plugin-transition-link/AniLink"

import Box from '../components/Box'
import Text from '../components/Text'
import Flex from '../components/Flex'
import Button from '../components/Button'
import Container from '../components/Container'

import Layout from '../containers/Layout'
import { responsive } from '../components/ThemeProvider/theme';
import { trackAnswer } from '../utils/trackEvent';

const Question = ({ data, pageContext: { id, isLast, length } }) => {
  const { values, submitForm } = useFormikContext()
  if(values.answers.length < (+id - 1)) {
    navigate(`/q/${values.answers.length + 1}`)
    return null
  }
  return (
    <Layout title={`第${id}題`} bg="white">
      <Container>
        <Flex alignItems="center" justify="space-between" pb="0.25em" borderColor="darkBlue" borderBottom="1px solid">
          <IconButton
            as={AniLink}
            swipe
            direction="right"
            w="1.5em"
            h="1.5em"
            mr="0.25em"
            minWidth="auto"
            bg="transparent"
            color="custom.darkBlue"
            icon={<FiArrowLeft size="2em" />}
            to={+id > 1 ? `/q/${id - 1}` : '/'}
          />
          <Text fontSize={responsive('1.5em', '1.25em')} letterSpacing="0.125em" fontFamily="Flama">{id}/{length}</Text>
        </Flex>
        <Text fontSize={responsive('1.125em', '1.25em')} mt="0.5em">{data?.questionsJson.question}</Text>
        <Box>
          {data?.questionsJson.options.map((opt, i) => (
            <Box mt="1em" key={i}>
              <Button
                as={!isLast && ((p) => <AniLink top="entry" {...p} />)}
                onClick={() => {
                  set(values, ['answers', +id - 1], i)
                  trackAnswer(id, i)
                  if(isLast) {
                    submitForm()
                  }
                }}
                to={!isLast && `/q/${+id + 1}`}
                swipe
                direction="left"
                width="100%"
              >
                {opt}
              </Button>
            </Box>
          ))}
        </Box>
      </Container>
    </Layout>
  )
}

export default Question

export const query = graphql`
  query QuestionPageQuery($id: String) {
    questionsJson(id: {eq: $id}) {
      question
      options
    }
  }
`


